// Share this
#block-bean-share-this {
  @extend %band;

  padding: uikit-space(0.7) 0;

  @include uikit-media(sm) {
    padding: uikit-space(1) 0;
  }

  @extend %colour-light-background-reverse;
  .block__title {
    @extend .uikit-display-2;
    font-weight: 700;
    margin:0;
  }
}

// Is there something wrong with this page.
#block-bean-page-accessibility-link {
  padding: 0 0 uikit-space(1) 0;
  @extend .container;
  .entity-bean {
    @extend .row;
    .field-name-field-bean-body {
      @extend .col-xs-12;
    }
  }
}

// Listing back button band.
.listing-back {
  @extend %band;
  padding: uikit-space(0.8) 0 uikit-space(0.8) 0;
  .wrapper {
    @extend .container;
    text-align: center;
  }
  a {
    font-size: 1em;
    @include uikit-media(md) {
      font-size: 1.1em;
    }
    @extend %baseicon;
    &.listing-back__icon:before {
      content: "\f060";
    }
  }
}

#block-bean-feedback-band,
#block-bean-homepage-about-us {
  h2 {
    @extend .uikit-display-4;
    max-width: 60rem;
  }
  p:last-of-type {
    margin-bottom: 0;
  }
}

// Landing pages only.
body {
  &.node-type-health-topic,
  &.node-type-health-topic-hp,
  &.node-type-landing-page {

    // All paragraph bands
    .para-container {
      @extend %band;
    }

    // Topic feature (nested paragraphs)
    // Container for large and small features
    .paragraphs-item-two-columns {
      // Style band
      &.para-container {
        @extend %band--neutral-darkest;
      }

      &.resources {
        background: white;
      }

      // Layout
      @include row-and-container;
      > .para-row {
        > .group-header,
        > .group-footer {
          @extend .col-xs-12;
        }
        > .group-left,
        > .group-right {
          @extend .col-xs-12;
          @extend .col-sm-6;
        }
      }
    }

    // Main feature
    .paragraphs-item-featured-news-events,
    .paragraphs-item-featured-news-events-hp {
      padding: 0;
      &.inside-para-container {
        padding: 0;
      }

    }
    // Sub features
    .paragraphs-item-para-view {
      padding: 0;
    }

    // contact hotline
    .paragraphs-item-para-contact {
      padding: 0;
      text-align: center;
      .view-mode-hotline_bar {
        padding: uikit-space(0.5);
        // Layout
        .group-left {
          @extend .col-xs-12;
          @extend .col-sm-6;
        }
        .group-right {
          @extend .col-xs-12;
          @extend .col-sm-6;
          .paragraphs-item-para-telephone {
            padding: 0;
            @include uikit-media(sm) {
              text-align: center;
            }
            .field-name-field-contact-telephone {
              @extend %list-item-title--large;
              a {
                border: none;
              }
            }
          }
        }
      }
    }

    // 2 col resources.
    .paragraphs-2-columns-resources {
      // Style band
      &.para-container {
        @extend %band--primary;
      }
      // Title/summary
      .group-header {
        @extend .col-xs-12;
      }
    }

    // Home page
    .page-landing {
      .paragraphs-2-columns-resources {
        background: white;
      }
    }

    // Video band
    .paragraphs-item-para-reference-video {
      // Style band
      &.para-container {
        @extend %band;
      }
      @include row-and-container;
    }

    .paragraphs-item-featured-resource {
      padding-top: 0;
      padding-bottom: 0;
    }

    // Document links
    .para-container {
      &.paragraphs-item-image {
        padding-top: 0;
        padding-bottom: uikit-space(1);
      }
    }

    // Text content.
    .para-container.paragraphs-item-para-content-text {
      @extend %band--neutral;
      @extend .container;
      .field-name-field-body {
        @extend .row;
      }
    }

    // Statistics
    .paragraphs-item-para-statistics {
      @extend %colour-light-background-reverse;
    }
  }
}